* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer {
  margin-top: 10px;
  display: flex;
  gap: 30px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
}

.value {
  font-size: 54px;
  line-height: 1;
}

.field {
  display: flex;
  flex-direction: column;
}

.form {
  display: flex;
  gap: 30px;
}

.form > label {
  display: flex;
  flex-direction: column;
  max-width: 200px;
}

.form > button {
  align-self: flex-end;
}
